import React, { Component } from 'react';
import { connect } from 'react-redux';
import { BootstrapTable, TableHeaderColumn } from 'react-bootstrap-table';
import * as action from '../state/actions/actions';
import CircularProgress from '@material-ui/core/CircularProgress';


const CaseTable = (props) => {

  var data = props.data;


  let table = null;
  if (data.length > 0) {
    const tableRows = data.map((caseItem) => {

      const date = new Date(parseInt(caseItem['case-started-at']));
      const options = { year: 'numeric', month: 'numeric', day: 'numeric' };
      var status = null;
      var color = null;
      var slaStatus = null;
      var slaColor = null;
      if (caseItem['case-status'] === 1) {
        status = 'OPEN';
        color = '#0092E1'
      } else if (caseItem['case-status'] === 2) {
        status = 'CLOSE';
        color = '#19AA89'
      } else if (caseItem['case-status'] === 3) {
        status = 'CANCELLED';
        color = '#F85555'
      }


      if (caseItem['case-sla-compliance'] === 1) {
        slaStatus = 'Pending';
        slaColor = '#FDA337';
      } else if (caseItem['case-sla-compliance'] === 2) {
        slaStatus = 'Met';
        slaColor = '#6CC363';
      } else if (caseItem['case-sla-compliance'] === 3) {
        slaStatus = 'Violated';
        slaColor = '#EB5757';
      } else {
        slaStatus = 'No SLA due';
        slaColor = '#8C8C8C';
      }


      return (
        <tr key={caseItem['case-id']} onClick={() => props.handleSelectCase(caseItem['case-id'])} style={{ cursor: 'pointer' }} >
          <td></td>
          <td>{date.toLocaleDateString('en-US', options)}</td>
          <td>{caseItem['case-id']}</td>
          <td>{caseItem['case-description']}</td>
          <td>{caseItem['case-owner']}</td>
          <td><font color={color}>{status}</font></td>
          <td><div style={{
            borderRadius: '50px/30px',
            background: slaColor,
            padding: '3px',
            paddingLeft: '30px',
            paddingRight: '30px',
            width: '100%',
            display: 'table-cell',
            verticalAlign: 'middle'
          }}><font color='white'>{slaStatus} </font> </div></td>
        </tr>

      );

    })
    table =
      <table className="table" id="myTable" style={{ fontSize: "10.79px" }}>
        <thead>
          <td></td>
          <td><span style={{ paddingLeft: "20px", fontWeight: "bold" }}> Date</span> </td>
          <td><span style={{ paddingLeft: "28px", fontWeight: "bold" }}> Case # </span></td>
          <td><span style={{ paddingLeft: "10px", fontWeight: "bold" }}> Description </span></td>
          <td><span style={{ paddingLeft: "5px", fontWeight: "bold" }}> Assigned to </span></td>
          <td><span style={{ paddingLeft: "8px", fontWeight: "bold" }}> Status </span></td>
          <td><span style={{ paddingLeft: "20px", fontWeight: "bold" }}> SLA Status </span></td>
        </thead>
        <tbody>
          {tableRows}
        </tbody>
      </table>
  }

  return (
    <div>
      <span style={{ paddingLeft: "50px" }}>{table}</span>
    </div>
  );
};
const TaskTable = (props) => {

  var data = props.data;


  let table = null;
  if (data.length > 0) {
    const tableRows = data.map((taskItem) => {

      return (
        <tr key={taskItem['task-id']} >
          <td>{taskItem['task-id']}</td>
          <td>{taskItem['task-name']}</td>
          <td>{taskItem['task-status']}</td>
          <td>{taskItem['task-description']}</td>
        </tr>

      );

    })
    table =
      <table className="table" id="myTable" >
        <thead>
          <td><span style={{ paddingLeft: "2px", fontWeight: "bold" }}> Task # </span></td>
          <td><span style={{ paddingLeft: "45px", fontWeight: "bold" }}> Task Name </span></td>
          <td><span style={{ paddingLeft: "5px", fontWeight: "bold" }}> Status </span></td>
          <td><span style={{ paddingLeft: "70px", fontWeight: "bold" }}>Description</span></td>
        </thead>
        <tbody>
          {tableRows}
        </tbody>
      </table>
  }

  return (
    <div>
      {table}
    </div>
  );
};
class MyWorkListApp extends Component {
  state = {
    showTaskList: false
  }

  componentDidMount() {
    const accessToken = localStorage.getItem("accessToken");
    // const accessToken = "8b0fdbd02fcee4260275b60c940da403";
    if (accessToken) {
      this.props.remoteGetCaseList(accessToken);
      this.props.remoteGetWorkList(accessToken)
    }
  }

  handleSelectCase = (caseId) => {
    const accessToken = localStorage.getItem("accessToken");
    // const accessToken = "8b0fdbd02fcee4260275b60c940da403";
    this.props.selectCase(caseId);
    this.props.remoteGetCaseDetails(accessToken, caseId);
  }

  render() {
    const caseList = this.props.caseList;
    const workList = this.props.workList;
    var tabel = null;
    var myPending = 0;
    var myViolated = 0;
    var myMet = 0;
    var noSla = 0;

    const caseDetails = this.props.caseDetails;
    const selectedCase = this.props.selectedCase;
    const loading = this.props.loading;

    if (loading == true) {
      tabel = <CircularProgress style={{ marginLeft: "30%" }} />;
    }

    if (caseDetails != null) {
      Promise.resolve(
        localStorage.setItem('caseId', selectedCase)
      )
        .then(() => {
          localStorage.setItem('customerName', caseDetails['case-details'].name + " " + caseDetails['case-details'].lname);
        })
        .then(function () {
          window.location = 'http://' + window.location.host + '/fsi-credit-card-dispute-backoffice/en/admin_task_detail.page';
        })
    }
    const showTaskList = this.state.showTaskList;

    if (caseList != null) {
      tabel = <CaseTable
        data={caseList}
        handleSelectCase={(caseId) => this.handleSelectCase(caseId)}
      />;
    }
    if (showTaskList && workList != null) {
      tabel = <TaskTable data={workList} />;
    }




    if (caseList != null) {
      //console.log(caseList[0]);
      for (var i = 0; i < caseList.length; i++) {
        if (caseList[i]['case-sla-compliance'] == 1) {
          myPending++;
          //        pending++;
        } else if (caseList[i]['case-sla-compliance'] == 2) {
          myMet++;
        } else if (caseList[i]['case-sla-compliance'] == 3) {
          myViolated++;
        } else if (caseList[i]['case-sla-compliance'] == 0) {
          noSla++;
        }

      }
    }


    return (
      <div className="ibox float-e-margins" style={{ borderTop: "solid", borderColor: "#0E65A0", borderWidth: '3.08px' }}>
        <div className="ibox-title">
          <h2><a onClick={() => this.setState({ showTaskList: false })} > {this.state.showTaskList ? <p style={{ color: '#b6b7ba', fontFamily: 'Open Sans', size: "18.5px", display: "inline" }}> Case List </p> : <p style={{ color: '#000000', fontFamily: 'Open Sans', size: "18.5px", display: "inline" }}> Case List </p>} </a>
            <p style={{ color: '#b6b7ba', fontFamily: 'Open Sans', size: "18.5px", display: "inline" }}> | </p>
            <a onClick={() => this.setState({ showTaskList: true })} >
              {this.state.showTaskList ? <p style={{ color: '#000000', fontFamily: 'Open Sans', size: "18.5px", display: "inline" }}> Task List </p> : <p style={{ color: '#b6b7ba', fontFamily: 'Open Sans', size: "18.5px", display: "inline" }}> Task List </p>}
            </a></h2>
        </div>
        <div className="ibox-content" id="main-contents">
          <div className="" style={{ fontFamily: "Open Sans" }}>
            <div className="row">
              <div className="col-sm-5 m-b-xs" />
              <div className="col-sm-4 m-b-xs" />
              <div className="col-sm-3" />
            </div>
            <div className="row">
              <div className="col-sm-12" />
            </div>

            <div className="row">
              {this.state.showTaskList ? (

                // start of task table list
                <div>
                  <div style={{ paddingLeft: "150px", lineHeight: "19px" }}>

                    <p style={{ color: "#898989", fontSize: "12.33px", display: "inline", fontWeight: "Bold" }}> SHOW  </p>

                    <div style={{ paddingLeft: '21.31px', display: 'inline', height: "25px" }}>
                      <select name="select" style={{ width: "114px", height: "25px" }}>
                        <option value="select"> All Cases </option>
                      </select>
                    </div>

                    <div class="search-container" style={{ paddingLeft: '9px', display: "inline-block" }}>
                      <form>
                        <input type="text" placeholder="Search" name="search" style={{ width: '213px', height: "23px", border: 'true' }} />
                        <button type="button" style={{ borderTopRightRadius: "5px", borderBottomRightRadius: "5px", float: "right", paddingLeft: "3px", backgroundColor: '#00659C', color: '#FFFFFF', height: '23.12px', width: '24.67px', textAlign: "left", fontSize: "9.25px" }}>Go!</button>
                      </form>
                    </div>

                    <div style={{ paddingLeft: '10.33px', display: 'inline' }}>
                      <input type="date" id="start" name="datepicker"
                        value="2018-09-25"
                        min="2018-01-01" max="2018-12-31" style={{ width: "168px", height: "25px" }} />
                    </div>
                    <div style={{ paddingLeft: '10px', position: 'absolute', display: 'inline-block' }}>
                      <i style={{ padding: '5px', paddingLeft: '10.02px', paddingTop: '3px', position: 'absolute', color: "#BDBDBD", borderLeftStyle: 'solid', borderWidth: "2px", lineHeight: "19.89px" }} class="fas fa-2x fa-print"></i>
                      <i style={{ paddingLeft: '52.33px', marginTop: '-1px', position: 'absolute', color: "#BDBDBD" }} class="fas fa-2x fa-file-download"></i>
                    </div>
                  </div>

                  <br /><br /><br />
                  {tabel}
                </div>
              )

                //end
                : (


                  // start of case table list


                  <div>
                    <div className="col-sm-2">
                      <p style={{ lineHeight: '19px', color: "#979797", fontSize: "14px", display: "inline", fontWeight: "600" }}>Quick Views</p> <br /><br />
                      <p style={{ lineHeight: '16px', color: "#EB5757", fontSize: "12px", display: "inline", fontWeight: "normal" }}>Violated: <span style={{ paddingLeft: '50px', fontSize: '24px', position: 'absolute', paddingTop: '-10px', }}>{myViolated}</span></p> <br /><br />
                      <p style={{ lineHeight: '16px', color: "#F39D3C", fontSize: "12px", display: "inline", fontWeight: "normal" }}>Pending: <span style={{ paddingLeft: '50px', fontSize: '24px', position: 'absolute', paddingTop: '-10px' }}>{myPending}</span></p> <br /><br />
                      <p style={{ lineHeight: '16px', color: "#6EC664", fontSize: "12px", display: "inline", fontWeight: "normal" }}>Met: <span style={{ paddingLeft: '75px', fontSize: '24px', position: 'absolute', paddingTop: '-10px' }}>{myMet}</span></p> <br /><br />
                    </div>
                    <div className="col-sm-10">

                      <div style={{ paddingLeft: "150px", lineHeight: "19px" }}>

                        <p style={{ color: "#898989", fontSize: "12.33px", display: "inline", fontWeight: "Bold" }}> SHOW  </p>

                        <div style={{ paddingLeft: '21.31px', display: 'inline', height: "25px" }}>
                          <select name="select" style={{ width: "114px", height: "25px" }}>
                            <option value="select"> All Cases </option>
                          </select>
                        </div>

                        <div class="search-container" style={{ paddingLeft: '9px', display: "inline-block" }}>
                          <form>
                            <input type="text" placeholder="Search" name="search" style={{ width: '213px', height: "23px", border: 'true' }} />
                            <button type="button" style={{ borderTopRightRadius: "5px", borderBottomRightRadius: "5px", float: "right", paddingLeft: "3px", backgroundColor: '#00659C', color: '#FFFFFF', height: '23.12px', width: '24.67px', textAlign: "left", fontSize: "9.25px" }}>Go!</button>
                          </form>
                        </div>

                        <div style={{ paddingLeft: '10.33px', display: 'inline' }}>
                          <input type="date" id="start" name="datepicker"
                            value="2018-09-25"
                            min="2018-01-01" max="2018-12-31" style={{ width: "168px", height: "25px" }} />
                        </div>
                        <div style={{ paddingLeft: '10px', position: 'absolute', display: 'inline-block' }}>
                          <i style={{ padding: '5px', paddingLeft: '10.02px', paddingTop: '3px', position: 'absolute', color: "#BDBDBD", borderLeftStyle: 'solid', borderWidth: "2px", lineHeight: "19.89px" }} class="fas fa-2x fa-print"></i>
                          <i style={{ paddingLeft: '52.33px', marginTop: '-1px', position: 'absolute', color: "#BDBDBD" }} class="fas fa-2x fa-file-download"></i>
                        </div>
                      </div>

                      <br /><br /><br />
                      {tabel}
                    </div>
                  </div>
                  // done
                )}
            </div>

          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  workList: state.workFlowReducer.workList,
  taskDetails: state.workFlowReducer.taskDetails,
  selectedTask: state.workFlowReducer.selectedTask,
  caseList: state.workFlowReducer.caseList,
  selectedCase: state.workFlowReducer.selectedCase,
  caseDetails: state.workFlowReducer.caseDetails,
  loading: state.workFlowReducer.loading,
});
const mapDispatchToProps = dispatch => ({
  remoteGetWorkList: (accessToken) => dispatch(action.remoteGetWorkList(accessToken)),
  remoteGetTaskDetails: (taskid) => dispatch(action.remoteGetTaskDetails(taskid)),
  selectTask: (taskid) => dispatch(action.selectTask(taskid)),
  remoteGetCaseList: (accessToken) => dispatch(action.remoteGetCaseList(accessToken)),
  selectCase: (caseId) => dispatch(action.selectCase(caseId)),
  remoteGetCaseDetails: (accessToken, caseId) => dispatch(action.remoteGetCaseDetails(accessToken, caseId)),
});
export default connect(mapStateToProps, mapDispatchToProps)(MyWorkListApp);


