export const getWorkList = (payload) => {
  return {
    type: GET_WORK_LIST,
    payload: payload,
  };
}
export const getTaskDetails = (payload) => {
  return {
    type: GET_TASK_DETAILS,
    payload: payload,
  };
}

export const getCaseDetails = (payload) => {
  return {
    type: GET_CASE_DETAILS,
    payload: payload,
  };
}

export const getCaseList = (payload) => {
  return {
    type: GET_CASE_LIST,
    payload: payload,
  };
}
export const selectTask = (taskid) => {

  Promise.resolve(
    localStorage.setItem('taskid', taskid)
  )
    .then(function () {
      window.location = 'http://' + window.location.host + '/fsi-credit-card-dispute-backoffice/en/admin_task_detail.page';
      // window.location = 'http://' + window.location.host + '/fsi-credit-card-dispute-backoffice/en/admin_task_detail.page'+ "?taskid=" + taskid
      // window.location = 'http://localhost:3000/fsi-credit-card-dispute-backoffice/' + "?taskid=" + taskid

    })
  return {
    type: SELECTED_TASK,
    payload: taskid,
  };
}
export const selectCase = (caseId) => {
  return {
    type: SELECTED_CASE,
    payload: caseId,
  };
}
export const restCallSuccess = () => {
  return {
    type: REST_CALL_SUCCESS
  };
};
export const restCallStart = () => {
  return {
    type: REST_CALL_START
  };
};

export const restCallFail = (error) => {
  return {
    type: REST_CALL_FAIL,
    error: error
  };
};

export const remoteGetWorkList = (accessToken) => {
  return dispatch => {
    dispatch(restCallStart());

    var payloadArray = [];
    request
      .get(GET_WORK_LIST_ENDPOINT)
      .set('Content-Type', 'application/json')
      // .set('Accept', 'application/json')
      .set('Authorization', 'Bearer ' + accessToken)
      .then((res) => {
        payloadArray = res.text;
        const resJS = JSON.parse(payloadArray)
        const reformat = reformatWorkList(resJS);
        dispatch(restCallSuccess());

        // console.log("Server Response " + JSON.stringify(reformat));
        dispatch(getWorkList(reformat));
      })
      .catch((err) => {
        var tmp = {};

        tmp.errorMessage = err.message;
        tmp.errorResponse = err.response;
        dispatch(restCallFail(tmp.errorResponse));
      });
  }
}
export const remoteGetCaseList = (accessToken) => {
  return dispatch => {
    dispatch(restCallStart());

    // console.log("Action " + constant.GET_NOTIFICATION_HISTORY);
    request
      .get(GET_CASE_LIST_ENDPOINT)
      .set('Content-Type', 'application/json')
      .set('Accept', 'application/json')
      .set('Authorization', 'Bearer ' + accessToken)
      .then((res) => {
        dispatch(restCallSuccess());

        const response = res.body;
        // console.log("outputs " + JSON.stringify(output));
        const instances = response.instances;
        // console.log("Server Response " + JSON.stringify(instances));

        return dispatch(getCaseList(instances));
      })
      .catch((err) => {
        var tmp = {};
        tmp.errorMessage = err.message;
        tmp.errorResponse = err.response;
        console.log(tmp);
        console.log(err);
        dispatch(restCallFail(tmp.errorResponse));
      });
  }
}
